const arrow_left = document.querySelector('.slider__arrow-left')
const arrow_right = document.querySelector('.slider__arrow-right')


const slider_faces = document.querySelectorAll('.svg-face')


const middleFaceIndex = Math.floor(slider_faces.length / 2);

let currentFaceIndex = 0;

slider_faces[currentFaceIndex].classList = ' svg-face svg-face--active'



const moveRight = () => {
  console.log("Prawo")
  slider_faces[currentFaceIndex].classList = 'svg-face svg-face--right'

  currentFaceIndex--;
  if(currentFaceIndex < 0){
    currentFaceIndex = 0
    
  }

  slider_faces[currentFaceIndex].classList = 'svg-face svg-face--active'
}


const moveLeft = () => {
  console.log("Lewo")
slider_faces[currentFaceIndex].classList = 'svg-face svg-face--left'

  currentFaceIndex++;
  if(currentFaceIndex >= slider_faces.length){
    currentFaceIndex = slider_faces.length -1
  }


  slider_faces[currentFaceIndex].classList = 'svg-face svg-face--active'

}



arrow_left.addEventListener('click', moveRight)
arrow_right.addEventListener('click', moveLeft)